@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700,700i&display=swap);
.container{width:100%;height:auto;padding:0px 30px}.header-container{padding:15px 30px;background:#10101a;background:linear-gradient(0deg, #071622 0%, #15151f 100%, rgba(61,162,183,0) 100%);display:flow-root}.header-menu-container{align-items:center}.header-menu-container .header-menu{float:left;margin-left:30px;margin-right:30px;margin-top:5px;color:#5f8d71;transition:color 1s;cursor:pointer}.header-menu-container :hover{color:#17b353}.header-menu-container .header-menu-active{color:#007a2f}.header-logo{float:left;padding:15px;background-color:#fff}.header-logo .logo-title{color:#007a2f !important;font-size:18px;margin:0 auto}.link-container{display:inline-flex;margin:5px;position:relative}

